@import "style.css";

/*Стили страницы товара*/


.product_carousel_bootstrap {
    height: 776px;
   background-color:#F7F7F7;
   
   
}
.carousel_bootstrap_wrapper {
     max-width: 1600px;
     position: relative;
     margin: 0 auto;
}
.carousel_item_img {
    max-height: 776px;
  max-width: 1600px;
}
.carousel-control-next {
    border: none;
    background:none;
}
.carousel-control-next-icon:hover {
    background-image: url(img/slider_right.svg);
    transform: none;
}
.carousel-control-next_background {
    position: absolute;
    right: 0;
    border: none;
    width: 47px;
    height: 47px;
    background: rgba(42, 42, 42, 0.15);
    display: flex;
    justify-content: center;
    align-items: center;
}
.carousel-control-next-icon {
  
    background-image: url(img/slider_left.svg);
    transform: rotate(180deg);
}

.carousel-control-prev-icon {
    background-image: url(img/slider_left.svg);
}
.carousel-control-prev-icon:hover {
    background-image: url(img/slider_right.svg);
    transform: rotate(180deg);
}

.carousel-control-prev {
  
    border: none;
    background:none;
}
.carousel-control-prev_background {
    position: absolute;
    left: 0;
    border: none;
    width: 47px;
    height: 47px;
    background: rgba(42, 42, 42, 0.15);
    display: flex;
    justify-content: center;
    align-items: center;
}

.product_info_wrapper {
    background: none;
    position: relative;
    display: flex;
    justify-content: center;
height: 664px;
}
.product_info {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    width: 1140px;
    max-height: 650px;
    top: -64px;
    background-color: #fff;
    border: 1px solid #EAEAEA;
    z-index: 2;
    margin: 0 auto;
    padding: 64px;
}

.product_info__subtitle {
    font-size: 14px;
    font-weight: 300;
    color:#F16D7F;
    margin: 0;
    
}
.product_info__title {
    font-size: 18px;
    font-weight: 300;
    color: #4D4D4D;
}

.bar {
    height: 3px;
    width: 63px;
    background: #EF5B70;
    margin-top: 12px;
    margin-bottom: 12px;
   
}
.product__description {
    width: 550px;
    font-size: 14px;
    font-weight: 300;
    line-height: 16.8px;
    color:  #5E5E5E;
    text-align: center;
    margin-top: 48px;
    margin-bottom: 32px;
}

.product_price {
    font-size:24px;
    font-weight: 300;
    color:#EF5B70;
}
.product__filters {
    width: 641px;
    border-top: 1px solid #EAEAEA;
    margin-top: 65px;
}
.product__filters_row {
    width: 375px;
    display: flex;
    justify-content: space-between;
    margin: 65px auto 48px auto;
    
    height: 30px;
    transition: height 0.3s ease-in;
}
.product__filters_summary {
    outline: none;
     background-color: #fff;

   

}
.product__filter {
    user-select: none;
    display: block;
    padding-bottom: 16px;
    transition: max-height 0.3s ease-in, box-shadow 0.2s ease;
}
::-webkit-details-marker {display: none}

.product__filter [open] .filter__color_checkbox{
    width: 100px;
    height: 200px;
    background: #ffffff;
    box-shadow: 6px 4px 35px rgba(0, 0, 0, 0.21);
    z-index: 4;
}



.product__cart_button {
    display: flex;
    justify-content: center;
margin: 0 auto;
    display: flex;
height: 47px;
width: 211px;
background: none;
border: 1px solid #FF6A6A;
text-align: center;
align-items: center;
text-decoration: none;
padding-left: 23px;
padding-right: 56px;
}

.product__cart_button_icon {
    background-image: url(img/basket_color.svg);
    background-size: 100%;
    background-repeat: no-repeat;
    height: 26px;
    width: 24px;
    margin-right: 11px;
    margin-bottom: 7px;
    margin-top: 10px;
}
.product__cart_button_text {
    font-size: 16px;
    color:  #FF6A6A;
    font-size: 14px;
}
.product__cart_button:hover {
    color: #fff;
    background-color: #FF6A6A;
}
.product__cart_button:hover .product__cart_button_icon {
background-image: url(img/basket.svg);

}
 
.product__cart_button:hover .product__cart_button_text {
    color: #fff;
}

.items__cards {
    margin-top: 0;
    margin-bottom: 24px;
    gap: 30px;
}